export default "import React, { useCallback, useEffect, useState } from 'react';\nimport useEmblaCarousel from 'embla-carousel-react';\nimport { NextButton, PrevButton, usePrevNextButtons } from '../EmblaCarouselArrowButtons';\n\nconst EmblaCarousel = (props) => {\n    const { slides, options } = props;\n    const [emblaRef, emblaApi] = useEmblaCarousel(options);\n    const [scrollProgress, setScrollProgress] = useState(0);\n    \n    const { prevBtnDisabled, nextBtnDisabled, onPrevButtonClick, onNextButtonClick } = usePrevNextButtons(emblaApi);\n    \n    const onScroll = useCallback((emblaApi) => {\n        const progress = Math.max(0, Math.min(1, emblaApi.scrollProgress()));\n        setScrollProgress(progress * 100);\n    }, []);\n    \n    useEffect(() => {\n        if (!emblaApi)\n            return;\n        \n        onScroll(emblaApi);\n        emblaApi.on('reInit', onScroll);\n        emblaApi.on('scroll', onScroll);\n    }, [emblaApi, onScroll]);\n    \n    return (<div className=\"embla\">\n      <div className=\"embla__viewport\" ref={emblaRef}>\n        <div className=\"embla__container\">\n          {slides.map((index) => (<div className=\"embla__slide\" key={index}>\n              <div className=\"embla__slide__number\">\n                <span>{index + 1}</span>\n              </div>\n            </div>))}\n        </div>\n      </div>\n \n      <div className=\"embla__controls\">\n        <div className=\"embla__buttons\">\n          <PrevButton onClick={onPrevButtonClick} disabled={prevBtnDisabled}/>\n          <NextButton onClick={onNextButtonClick} disabled={nextBtnDisabled}/>\n        </div>\n \n        <div className=\"embla__progress\">\n          <div className=\"embla__progress__bar\" style={{ transform: `translate3d(${scrollProgress}%,0px,0px)` }}/>\n        </div>\n      </div>\n    </div>);\n};\n\nexport default EmblaCarousel;\n";